import React, { useRef } from "react";

const VideoSearch = ({ onSearch }) => {
  const inputRef = useRef();

  const handleSearch = () => {
    const value = inputRef.current.value;
    onSearch(value);
  };

  const onClick = () => {
    //console.log("onclick");
    handleSearch();
  };
  const onKeyPress = (event) => {
    //console.log("onKeyPress");
    if (event.key === "Enter") {
      handleSearch();
    }
  };

  return (
    <div className="title">
      <h2>검색하기</h2>
      <input
        className="youtube_search_input"
        ref={inputRef}
        type="search"
        placeholder="동영상을 검색하세요."
        onKeyPress={onKeyPress}
      />
      <button className="youtube_search_btn" type="submit" onClick={onClick}>
        검색
      </button>
    </div>
  );
};

export default VideoSearch;
